.not-found-page {
    width: 100%;
    color: white;
    display: flex;
    flex-direction: column;
}

.not-found-title {
    font-size: 32px;
    font-weight: bold;
    padding: 20px 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 0 1 100%;
    text-align: center;
}

.not-found-icon {
    font-size: 128px;
    box-shadow: inset black;
    color: white;
    text-shadow: 0px 0px 5px rgba(255, 255, 255, 1);
}

.not-found-links{
    margin-top: 10px;
    text-align: center;
}