.profile-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex: 0 1 100%;
}

.profile-sidebar {
    width: 300px;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.menu-item {
    width: 230px;
    height: 38px;
    min-height: 38px;
    padding: 0px 5px;
    display: flex;
    flex-direction: row;
    color: #fdfdfd;
    font-size: 14px;
    font-family: 'Medium';
    cursor: pointer;
    border-bottom: transparent solid 2px;
}

.menu-item:hover {
    border-bottom: #7fff7f solid 2px;
}

.menu-item.active {
    background-color: rgba(128, 128, 128, 0.6);
    pointer-events: none;
}

.menu-icon {
    width: 60px;
    height: 30px;
    display: inline-block;
    margin-right: 25px;
    background-position: center;
}

.profile-card {
    display: flex;
    flex-direction: column;
    flex: 0 1 100%;
    background-color: #660066;
    padding: 20px;
}

.profile-card .title {
    padding: 10px 0px;
    font-size: 20px;
}

.profile-card .tab-container {
    display: flex;
    flex-direction: column;
    background-color: #800080;
}

.profile-card .tab-nav {
    display: flex;
    flex-direction: row;
}

.profile-card .tab {
    width: 20%;
    height: 40px;
    color: #ffffff;
    font-size: 14px;
    font-family: 'Bold';
    background-color: #991199;
    cursor: pointer;
    display: flex;
    text-align: center;
}

.profile-card .tab:hover {
    background-color: #973e95;
}

.profile-card .tab.active {
    background-color: #800080;
    pointer-events: none;
}

.profile-card .tab-body {
    display: flex;
    flex-direction: column;
    flex: 0 1 100%;
    background-color: #800080;
    padding: 20px 30px;
}

.profile-card .tab-body .form-row {
    width: 100%;
}

.profile-card .tab-body .edit-btns .btn {
    width: 150px;
    margin-left: 30px;
}

.profile-card .tab-body>.form-row>.btn, .profile-card .tab-body>.form-row>.form-cell {
    margin: 0px 15px;
}

.profile-card .tab-body .photo-cell {
    width: 150px;
    max-width: 150px;
}

.profile-card .profile-photo {
    width: 120px;
    height: 120px;
    border: 2px solid #ffffff;
    border-radius: 100px;
    overflow: hidden;
}

.profile-card .profile-photo img{
    width: 120px;
    height: 120px;
    object-fit: cover;
}

.profile-card .input-checkbox-container:not(:last-child) {
    margin-right: 10px;
}

.profile-card .input-checkbox-label {
    font-size: 14px;
}

.profile-card .tab-body.trips-container {
    display: flex;
    flex-direction: row;
    ;
}

.trips-active {
    display: flex;
    flex: 0 1 100%;
    flex-direction: column;
}

.trips-active .title, .trips-create .title {
    font-size: 14px;
    margin-bottom: 20px;
}

.trips-active .filter {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.trips-active .filter .svg {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}

.trips-active .filter .input {
    width: 170px;
    margin: 0px 15px;
}

.trips-active .table {
    margin-top: 20px;
}

.trips-active .btn, .trips-create .btn {
    width: 170px;
}

.trips-create {
    width: 250px;
    margin-left: 30px;
    padding: 0px 0px 0px 30px;
    border-left: 2px solid #991199;
}

.orders-container {
    display: flex;
    flex: 0 1 100%;
    flex-direction: column;
}