.table {
    border-collapse: collapse;
}

.table .cell {
    text-align: center;
}

.table .table-header .cell {
    color: #cec8c8;
    font-size: 10px;
    font-family: 'Bold';
    padding-bottom: 5px;
}

.table .table-body {
    border: #ffffff solid 1px;
}

.table .table-body .row {
    height: 40px;
    color: #ffffff;
    font-size: 12px;
    font-family: 'Regular';
}

.table .table-body .row:nth-child(odd) {
    background-color: #973e95;
}

.table .table-body .row:nth-child(even) {
    background-color: #ad66a9;
}

.table .table-body .row:hover {
    height: 40px;
    border: #00bfff solid 1px;
}

.table .table-body .row:active {
    background-color: #800080;
}

.table .table-body .cell {
    position: relative;
}

.table .table-body .cell:not(:last-child)::after {
    content: "";
    width: 0px;
    height: 25px;
    position: absolute;
    right: 0px;
    top: 7.5px;
    border: 1px solid #404040;
}