html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow: hidden;
}

.app {
  width: 100%;
  height: 100%;
  background-color: #660066;
  display: flex;
}

.app.light {
  background-color: #800080;
}

.app-background {
  width: 100%;
  height: 100%;
  background-image: url(../resources/img/flying-taxi.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.app-container {
  width: 100%;
  height: 100%;
  background: rgba(128, 0, 128, 0.5);
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.app-logo {
  display: flex;
  align-items: center;
  padding: 5px;
}

.app-logo .logo {
  width: 25px;
  height: 25px;
  display: inline-block;
  margin-right: 15px;
}

.align-center {
  align-items: center;
}

.justify-start {
  justify-content: start!important;
}

.justify-center {
  justify-content: center;
}

.justify-right {
  justify-content: flex-end!important;
}

.link {
  color: white;
  text-decoration: underline;
  cursor: pointer;
}

.link:hover {
  color: black;
}

.title {
  color: white;
  font-size: 16px;
  font-family: 'Bold';
}

.title.grid-title {
  color: #cec8c8;
  font-size: 10px;
}

/** Inputs **/

.input-label {
  color: #cec8c8;
  font-size: 12px;
  font-family: 'Bold';
}

.input, .select>div {
  border: #00bfff solid 2px;
  border-radius: 5px;
  color: #000000;
  font-size: 14px;
  font-family: 'Regular';
}

.select>div:hover {
  border: #00bfff solid 2px!important;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 1px 1px #4d90f0;
}

.input:focus::-moz-placeholder {
  color: transparent;
}

.input:focus::-webkit-input-placeholder {
  color: transparent;
}

.input::placeholder {
  color: #747272;
  font-size: 10px;
  font-family: 'Bold';
}

.input.invalid {
  border: #ff1100 solid 2px;
}

.input.invalid:focus {
  box-shadow: 0 0 1px 1px #ff2200;
}

.input-label {
  width: 100%;
  margin-bottom: 5px;
}

.input-text {
  width: 324px;
  height: 34px;
  padding-left: 20px;
}

.input-date {
  width: 156px;
  height: 34px;
}

.input-password {
  width: 346px;
  height: 34px;
}

.input-checkbox-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.input-checkbox {
  width: 21px;
  height: 21px;
  padding: 0px!important;
  margin: 0px!important;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  flex-direction: row;
}

.input-checkbox-label {
  margin-left: 5px;
}

.locked-input {
  width: 310px;
  height: 38px;
  display: flex;
  border-bottom: #00bfff solid 2px;
  color: #ffffff;
  font-size: 14px;
  font-family: 'Regular';
  padding: 0px 10px 0px 30px;
}

.input.short, .locked-input.short {
  width: 120px;
}

.input-file {
  position: relative;
  color: transparent;
}

.input-file label {
  color: #ffffff;
  position: absolute;
}

.note-danger {
  font-size: 10px;
  color: red;
}

.checkbox-input-container {
  width: 120px;
  display: flex;
  flex-direction: row;
  justify-items: center;
}

.select {
  width: 250px;
  height: 40px;
}

/** Buttons **/

.btn {
  border: #ffffff solid 2px;
  color: #ffffff;
  font-size: 12px;
  font-family: 'Bold';
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.btn.disabled {
  background-color: #ccc;
  pointer-events: none;
}

.btn.invisible {
  opacity: 0;
}

.btn-green {
  width: 350px;
  height: 40px;
  background-color: #7fff7f;
  color: #202020;
}

.btn-green:hover {
  background-color: #128312;
  color: #ffffff;
}

.btn-green:focus {
  outline: none;
  box-shadow: 0 0 1px 1px white;
}

.btn-blue {
  width: 100%;
  height: 40px;
  background-color: #00bfff;
}

.btn-blue:hover {
  background-color: #0087b4;
}

.btn-blue:focus {
  outline: none;
  box-shadow: 0 0 1px 1px white;
}

.btn-blue.btn-edit {
  width: 170px;
}

.btn-icon {
  width: 30px;
  height: 30px;
  display: inline-block;
  background-position: center;
  margin-right: 10px;
}

.invalid-text {
  color: #ff1100!important;
}

.filler {
  display: flex;
  flex: 0 1 100%;
}

.context-menu {
  position: fixed;
  background-color: #fafafa;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px 0px;
  z-index: 100;
}

.context-menu .context-menu-item {
  padding: 5px 20px;
  cursor: pointer;
}

.context-menu .context-menu-item:hover {
  background-color: #dedede;
}